<template>
  <!-- 内部记账依据明细表 -->
  <div>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
        <el-form-item label="财务组织" size="mini">
          <el-select v-model="params.company_id" placeholder="请选择" :filter-method="companyListInvok" @focus="companyListInvok" @change="companyChange" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini">
          <el-date-picker v-model="timeValue1" @change="change" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="审核日期" size="mini">
          <el-date-picker v-model="timeValue" @change="change" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="部门" size="mini">
          <el-select v-model="params.department_id" @focus="DepartmentListInvok" @change="departmentChange" placeholder="请选择">
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务员" size="mini">
          <el-select v-model="params.business_id" @focus="EmployeeListInvok" @change="employeeChange">
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="往来类型" size="mini">
          <el-select clearable v-model="params.accounts_type_id" placeholder="请选择" @focus="getCurrentAccountListInvok" @change="accountsTypeChange" filterable>
            <el-option :label="item.accounts_type_name" :value="item.accounts_type_id" :key="item.accounts_type_id" v-for="item in accountypeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="结算方式" size="mini">
          <el-select clearable v-model="params.receivables_mode_id" @focus="getNewrece" @change="getPaymentChange">
            <el-option v-for="item in receivablesList" :label="item.receivables_mode_name" :value="item.receivables_mode_id" :key="item.receivables_mode_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="创建人" size="mini">
          <el-select v-model="params.add_user_id" filterable @focus="getaddUserListFocus" @change="addUserChange">
            <el-option :label="item.realname" :value="item.user_id" :key="item.user_id" v-for="item in addUserList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户名称" size="mini">
          <el-select v-model="params.customer_id" filterable clearable placeholder="请输入" @change="customerChange" @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>

      </el-form>
    </div>
    <div class="add_delet" v-if="authority.b_export==1 && listShow">
      <el-dropdown placement="bottom-start">
        <span split-button style="cursor:pointer;color:#4e90e2">
          <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
        </span>
        <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
          <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
          <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="table_container clearblue" v-if="listShow">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" :summary-method="getSummaries" show-summary :max-height="tableHeight">
        <el-table-column :show-overflow-tooltip="true" property="company_name" label="财务组织"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="department_name" label="部门"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_name" label="业务员"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="logistics_name" label="物流公司"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="logistics_number" label="物流票号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="accounts_type_name" label="往来类型"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="receivables_mode_name" label="结算方式"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="bank_name" label="开户银行"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="bank_number" label="银行账号后四位"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="common_price" label="金额" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" width="140" property="business_time" label="业务日期"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" width="140" property="auditor_time" label="审核日期"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="receipt_sn" label="收款单据号" width="160">
          <template slot-scope="scope">
            <el-button @click="seeDetail(scope,scope.row)" type="text" size="small">
              {{scope.row.receipt_sn}}
            </el-button>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_user_name" label="创建人"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注"></el-table-column>
      </el-table>
      <div class="table_info">
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <div class="report_row">收款金额合计：{{TableData.desc.all_price}}元</div>
          </el-col>
        </el-row>
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <div class="report_row">生成时间：{{TableData.desc.add_time}}</div>
          </el-col>
          <el-col :span="12">
            <div class="textAr report_row">制表人：{{TableData.desc.add_user_name}}</div>
          </el-col>
        </el-row>
        <el-row type="flex" class="row-bg report_row">
          <el-col :span="12">
            <div>业务日期起始:{{this.params.business_start_time}}</div>
          </el-col>
          <el-col :span="12">
            <div class="textAr">业务日期截止:{{this.params.business_end_time}}</div>
          </el-col>
        </el-row>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import {
  getData,
  opthionList,
  exportFile,
  tableResize //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
export default {
  name: "reportReceiptdetrptIndex",
  data() {
    return {
      authority: {},
      addUserList: [],
      timeValue1: [],
      params: {},
      searchParams: {},
      listShow: false,
      timeValue: [],
      exportType: null,
      TableData: {
        desc: {},
        tableData: []
      },
      pageSize: 30,
      goPage: 1,
      per_page: [30, 50, 100, 500],
      loading: false,
      tableHeight: 600
    };
  },
  computed: {
    ...mapGetters(["receiptdetrptIndexGet"])
  },
  methods: {
    ...mapActions(["receiptdetrptSeeAct"]),
    getNewrece() {
      console.log(1);
      if (this.params.company_id) {
        console.log(this.params.company_id);
        this.$axMethod.getCollectionList(this.params).then(({ data }) => {
          let result = data.data;
          this.$set(this, "receivablesList", result);
        });
      } else {
        this.$set(this, "receivablesList", []);
      }
    },
    customerChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    addUserChange(val) {
      // if (val) {
      //   this.params.realname = this.addUserList.options.filter(
      //     obj => obj.user_id === val
      //   )[0].realname;
      // }
    },
    getPaymentChange(val) {
      if (val) {
        this.params.receivables_mode_name = this.receivablesList.filter(
          obj => obj.receivables_mode_id === val
        )[0].receivables_mode_name;
      }
    },
    accountsTypeChange(val) {
      if (val) {
        this.params.accounts_type_name = this.accountypeList.filter(
          obj => obj.accounts_type_id === val
        )[0].accounts_type_name;
      }
    },
    employeeChange(val) {
      if (val) {
        this.params.employee_name = this.employeeList.filter(
          obj => obj.employee_id === val
        )[0].employee_name;
      }
    },
    seeDetail(scope, row) {
      this.receiptdetrptSeeAct({
        commont: "receiptdetrptSee",
        receipt_id: row.receipt_id,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });

      // this.$router.push({
      //   name: "receiptmanage",
      //   query: {
      //     receipt_id: row.receipt_id
      //   }
      // });
    },
    // getBm() {
    //   getDepartmentListCommon(this, this.params, 1);
    // },
    exportOrder(scheduleType) {
      // 导出采购信息汇总表
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      // let arg = {
      //   params: this.params,
      //   API: "exportCustomerContactsListUrl"
      // };
      // exportFile(arg);
      this.$axMethod.exportReceiptDetailUrl(this.params).then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    getaddUserListFocus() {
      // this.$common.options("getReceiptListXl", this.addUserList, {
      //   company_id: this.params.company_id
      // }); //创建人
      this.$axMethod
        .getReceiptAddUserNameList({ company_id: this.params.company_id })
        .then(response => {
          if (response.data.code == 200) {
            this.addUserList = response.data.data;
          }
        });
    },
    // getaccountsTypeFoucs() {
    //   this.$common.options("getCurrentAccountListOption", this.accountypeList); //往来类型
    // },
    exportFile(scheduleType) {
      //导出
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      let arg = {
        params: this.params,
        API: "exportReceiptDetailUrl"
      };
      exportFile(arg);
    },
    change(val) {
      //选择的日期
      this.params.start_time = val[0];
      this.params.end_time = val[1];
    },
    onReset() {
      this.timeValue = [];
      this.timeValue1 = [];
      // this.params = {};
      this.$set(this, "params", {});
      this.searchParams = {};
      let vm = this;
      this.$axios({
        method: "get",
        url: this.$api.getReceiptDetailList,
        params: this.params
      }).then(({ data }) => {
        if (data.code == 200) {
          vm.TableData = data.data;
          vm.TableData.tableData = data.data.data;
          vm.TableData.desc = data.data.desc;
          tableResize.bind(this)();
        }
      });
    },
    onSearch() {
      this.listShow = true;
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1 && this.timeValue1.length == 2) {
        this.params.business_start_time = this.timeValue1[0];
        this.params.business_end_time = this.timeValue1[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      this.searchParams = this.params;
      let vm = this;
      this.$axios({
        method: "get",
        url: this.$api.getReceiptDetailList,
        params: this.params
      }).then(({ data }) => {
        if (data.code == 200) {
          vm.TableData = data.data;
          vm.TableData.tableData = data.data.data;
          vm.TableData.desc = data.data.desc;
          tableResize.bind(this)();
        }
      });
    },
    companyChange(val) {
      // 获取部门
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "add_user_id", null);
      this.$set(this.params, "department_id", null);
      this.$set(this.params, "receivables_mode_id", null);
    },
    // companyFilter(val) {
    //   let options = {
    //     type: 2,
    //     company_name: val
    //   };
    //   getInventoryFinanceCompanyOpen(this, options);
    // },
    // getCompany() {
    //   let options = {
    //     type: 2,
    //     company_name: null
    //   };
    //   getInventoryFinanceCompanyOpen(this, options);
    // },

    departmentChange(val) {
      //获取业务员
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
      // getEmployeeListCommon(this, this.params, 1);
      this.$set(this.params, "employee_id", "");
    },
    handleSizeChange(val) {
      let vm = this;
      let params = {
        per_page: val,
        page: 1,
        company_id: this.params.company_id,
        department_id: this.params.department_id,
        business_id: this.params.business_id,
        start_time: this.params.start_time,
        end_time: this.params.end_time
      };
      this.$axios({
        method: "get",
        url: this.$api.getReceiptDetailList,
        params: params
      }).then(({ data }) => {
        if (data.code == 200) {
          vm.TableData = data.data;
          vm.TableData.tableData = data.data.data;
          vm.TableData.desc = data.data.desc;
        }
      });
    },
    handleCurrentChange(val) {
      let vm = this;
      Object.assign(this.params, {
        page: val
      });
      this.$axios({
        method: "get",
        url: this.$api.getReceiptDetailList,
        params: this.params
      }).then(({ data }) => {
        if (data.code == 200) {
          vm.TableData = data.data;
          vm.TableData.tableData = data.data.data;
          vm.TableData.desc = data.data.desc;
        }
      });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "本页合计";
          return;
        }
        if (index === 10) {
          sums[index] = this.TableData.desc.current_price;
          return;
        }
      });

      return sums;
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));

    if (this.receiptdetrptIndexGet && this.receiptdetrptIndexGet.params) {
      this.$set(this, "params", this.receiptdetrptIndexGet.params);
      // bindIdsF(this, this.receiptdetrptIndexGet.params, [
      //   ["company_id", "company_name", "companyList"],
      //   ["department_id", "department_name", "departmentList", "options"],
      //   ["customer_id", "customer_name", "customerList", "options"],
      //   ["accounts_type_id", "accounts_type_name", "accountype", "options"],
      //   ["employee_id", "employee_name", "employeeList", "options"],
      //   ["receivables_mode_id", "receivables_mode_name", "payment", "options"],
      //   ["add_user_id", "realname", "addUserList", "options", "realname"]
      // ]);
      opthionList(this, this.receiptdetrptIndexGet.params);
      this.addUserList = [
        {
          realname: this.receiptdetrptIndexGet.params.realname,
          user_id: this.receiptdetrptIndexGet.params.user_id
        }
      ];
      // this.params.
    }
    if (this.receiptdetrptIndexGet && this.receiptdetrptIndexGet.goPage) {
      this.params.page = this.receiptdetrptIndexGet.goPage;
      this.params.per_page = this.receiptdetrptIndexGet.pageSize;
    }
    // 查询
    this.listShow = true;
    if (this.timeValue && this.timeValue.length == 2) {
      this.params.start_time = this.timeValue[0];
      this.params.end_time = this.timeValue[1];
    } else {
      this.params.start_time = this.params.end_time = "";
    }
    let vm = this;
    this.$axios({
      method: "get",
      url: this.$api.getReceiptDetailList,
      params: this.params
    }).then(({ data }) => {
      if (data.code == 200) {
        vm.TableData = data.data;
        vm.TableData.tableData = data.data.data;
        vm.TableData.desc = data.data.desc;
        tableResize.bind(this)();
      }
    });
    //取消单据右侧滚动条
    tableResize.bind(this)();
    window.onresize = tableResize.bind(this);
  }
};
</script>

<style lang="less" scoped>
