<template>
  <div>
    <component v-bind:is="commont"></component>
  </div>
</template>

<script>
import receiptdetrptIndex from "./receiptdetrptIndex.vue";
import receiptdetrptSee from "./receiptdetrptSee.vue";
import { mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      commont: "receiptdetrptIndex"
    };
  },
  components: {
    receiptdetrptIndex, //初始化
    receiptdetrptSee //单据号详情页面
  },
  computed: {
    ...mapGetters(["receiptdetrptIndexGet", "receiptdetrptSeeGet"])
  },
  watch: {
    receiptdetrptIndexGet() {
      this.commont = this.receiptdetrptIndexGet.commont;
    },
    receiptdetrptSeeGet() {
      this.commont = this.receiptdetrptSeeGet.commont;
    }
  }
};
</script>